<template>
<div id="app">
    <el-row>
        <el-col :span="24">
            <el-form :inline="true" :model="form" ref="form" label-width="100px">
                <div style="margin-bottom: 5px;">
                    <el-select v-model="form.loglevel" placeholder="日志级别">
                        <el-option label="INFO" value="INFO"></el-option>
                        <el-option label="ERROR" value="ERROR"></el-option>
                    </el-select>
                    <el-select v-model="form.serviceId" placeholder="服务ID">
                        <el-option v-for="s in services" :label="s.serviceId" :value="s.serviceId"></el-option>
                    </el-select>
                    <el-select v-model="form.logSearchTime" placeholder="时间">
                        <el-option label="15分钟" value="15"></el-option>
                        <el-option label="今天" value="today"></el-option>
                        <el-option label="1小时" value="60"></el-option>
                        <el-option label="三小时" value="180"></el-option>
                        <el-option label="昨天" value="yesterday"></el-option>
                    </el-select>
                </div>
                <div style="margin-bottom: 5px;">
                    <el-input v-model="form.transId" placeholder="TransId" style="width: 390px"></el-input>
                    <el-date-picker v-model="form.startTime" type="datetime" placeholder="开始时间">
                    </el-date-picker>
                    <el-date-picker v-model="form.endTime" type="datetime" placeholder="结束时间">
                    </el-date-picker>
                </div>
                <div style="margin-bottom: 5px;">
                    <el-input v-model="form.key" placeholder="关键字" style="width: 400px"></el-input>
                    <el-select v-model="form.size" placeholder="日志条数">
                        <el-option label="100" value="100"></el-option>
                        <el-option label="500" value="500"></el-option>
                        <el-option label="1000" value="1000"></el-option>
                        <el-option label="5000" value="5000"></el-option>
                        <el-option label="10000" value="10000"></el-option>
                    </el-select>
                    <el-button type="primary" @click="query()" :loading="queryLoading">查询</el-button>
                    <el-button @click="cleanQuery()">清空</el-button>
                </div>
            </el-form>
        </el-col>
    </el-row>
    <div>
        <pre class="logDiv" id="logDiv"></pre>
    </div>
</div>
</template>

<script>
import log from 'components/api/tools/log.js'
import _ from 'underscore'
import jquery from 'jquery'
import catalog from 'components/api/auth/apicatalog.js'

export default {
    data() {
        return {
            form: {
                loglevel: '',
                serviceId: '',
                logSearchTime: '15',
                transId: '',
                startTime: '',
                endTime: '',
                key: '',
                size: '100'
            },
            logDiv: '',
            services: [],
            queryLoading: false
        };
    },
    methods: {
        query: function () {
            var _this = this;
            var data = _this.form;
            var queryList = [];

            //时间判断
            var searchTimeMinllis = parseInt(data.logSearchTime)
            var now = new Date();
            //今天开始时间
            var todayStartTime = now.getTime() - (now.getHours() * 3600000 + now.getMinutes() * 60000 + now.getSeconds() *
                1000 + now.getMilliseconds());
            var startTime = 0;
            var endTime = now.getTime();
            if (data.startTime && data.endTime) {
                startTime = Date.parse(data.startTime);
                endTime = Date.parse(data.endTime);
            } else {
                if (!isNaN(searchTimeMinllis)) {
                    startTime = endTime - searchTimeMinllis * 60 * 1000;
                } else if ("today" == data.logSearchTime) {
                    startTime = todayStartTime;
                } else if ("yesterday" == data.logSearchTime) {
                    endTime = todayStartTime;
                    startTime = endTime - 24 * 3600 * 1000;
                }
            }


            queryList.push({
                range: {
                    "@timestamp": {
                        "gte": startTime,
                        "lte": endTime != now.getTime() ? endTime : null, //TODO 消除当前时间也服务器不一致问题
                        "format": "epoch_millis"
                    }
                }
            })

            //对象判断
            var queryIndex = '*';
            //TODO 需要优化， 只查询必要的index

            //关键字
            if (data.key) {
                queryList.push({
                    query_string: {
                        default_field: "msg",
                        query: data.key ? ('*' + data.key + '*') : '*'
                    }
                });
            }
            //日志级别
            if (data.loglevel) {
                queryList.push({
                    match: {
                        loglevel: data.loglevel
                    }
                });
            }
            //应用类型
            if (data.serviceId) {
                queryList.push({
                    match: {
                        service_id: {
                            query: data.serviceId,
                            "operator": "and"
                        }
                    }
                });
            }

            //事务号
            if (data.transId) {
                queryList.push({
                    match: {
                        transId: data.transId
                    }
                });
            }

            var query = {
                size: data.size,
                sort: [{
                    "@timestamp": {
                        "order": "desc",
                        "unmapped_type": "boolean"
                    }
                }],
                query: {}
            };
            if (queryList.length > 0) {
                query.query.bool = {
                    must: queryList,
                    must_not: [{
                        match: {
                            class: 'DubboMonitor'
                        }
                    }]
                }
            } else {
                query.query = {
                    "match_all": {},
                    must_not: [{
                        match: {
                            class: 'DubboMonitor'
                        }
                    }]
                }
            };
            jquery("#logDiv").html("");
            _this.queryLoading = true;
            jquery.ajax({
                type: "post",
                url: "/logsearch/logstash-*/_search",
                contentType: "application/json; charset=utf-8",
                data: JSON.stringify(query),
                dataType: 'json',
                success: function (resp) {
                    var data = resp;
                    if (data.hits && data.hits.hits && data.hits.hits.length > 0) {
                        var list = data.hits.hits;
                        var content = [];
                        _.each(list, function (line) {
                            var d = line._source;
                            content.push(d.time);
                            content.push('-');
                            content.push(d.loglevel);
                            content.push('-');
                            content.push('[');
                            content.push(d.service_id);
                            content.push(']');
                            content.push('-');
                            content.push(d.transId);
                            content.push('-');
                            content.push(d.class);
                            content.push(':');
                            content.push(d.line);
                            content.push('-');
                            content.push(d.msg);
                            if (d.detail) {
                                content.push(d.detail);
                                if (d.detail.indexOf('\r') == -1) {
                                    content.push('\r');
                                }
                            } else {
                                if (!d.msg || d.msg.indexOf('\r') == -1) {
                                    content.push('\r');
                                }
                            }

                        })
                        jquery("#logDiv").html(content.join(""))
                    } else {
                        jquery("#logDiv").html("未查询到数据");
                    }
                    _this.queryLoading = false;
                },
                error: function () {
                    jquery("#logDiv").html("系统异常");
                    _this.queryLoading = false;
                }
            });
        }
    },
    mounted() {
        var _this = this;
        catalog.listAll().then(function (resp) {
            _this.services = resp.data;
            _this.services.unshift({
                serviceId: ''
            })
        })
    }
};
</script>
<style>
.logDiv {
    display: block;
    padding: 10px;
    font-size: 13px;
    line-height: 1.42857143;
    color: #333;
    word-break: break-all;
    word-wrap: break-word;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
    overflow-y: auto;
    overflow-x: auto;
}
</style>



